*{
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}
.main-body{
    padding: 22px;
    background-color:#F1F5F9;
    min-height: 130vh;
    
}
Nav{
    border-radius: 5px;
   
}
.container{
    min-height: 170vh;
    background: #FFFFFF;
    margin-top: 10vh;
    border-radius: 5px;
    width: 90%;
}
.space{
    height: 40vh;
    border-bottom: 1px solid rgba(172, 172, 172, 0.712);
    margin-bottom: 40px;
}
.controls{
    margin-top: 20px;
}

.center{
    display: flex;
    justify-content: center;
    align-content: center; 
}

.clipbord:hover{
    cursor: pointer;
    color: #CBD5E1;

}

.generatorBtn{
    width: 50%;
    margin: 20px auto;
    border-radius: 5px;
    background: #0F172A;
    color: white;
    font-size: 16px;
    font-weight: 600;
    padding: 20px;
    text-align: center;
    cursor: pointer;
}
.generatorBtn:hover{
    background: #0f172ab3;
}

#redSlider::-webkit-slider-thumb{
    height: 45px;
    width: 15px;
    background: #FFFFFF;
    border: 1px solid rgb(224, 224, 224);
    cursor: pointer;
    border-radius: 5px;
}
#greenSlider::-webkit-slider-thumb{
    height: 45px;
    width: 15px;
    background: #FFFFFF;
    border: 1px solid rgb(224, 224, 224);
    cursor: pointer;
    border-radius: 5px;
}
#blueSlider::-webkit-slider-thumb{
    height: 45px;
    width: 15px;
    background: #FFFFFF;
    border: 1px solid rgb(224, 224, 224);
    cursor: pointer;
    border-radius: 5px;
}
#alphaSlider::-webkit-slider-thumb{
    height: 45px;
    width: 15px;
    background: #FFFFFF;
    border: 1px solid rgb(224, 224, 224);
    cursor: pointer;
    border-radius: 5px;
}

#redSlider::-webkit-slider-runnable-track{
    background: #FF3F3F;
    height: 28px;

}
#greenSlider::-webkit-slider-runnable-track{
    background: #3FFF7F;
    height: 28px;

}
#blueSlider::-webkit-slider-runnable-track{
    background: #007FFF;
    height: 28px;

}
#alphaSlider::-webkit-slider-runnable-track{
    background: linear-gradient(90deg, #FCFCFC , #020202);
    height: 28px;

}

.input{
    background: #F8FAFC;
}

#previewBox{
    height: 250px;
    margin: auto;
    border-radius: 15px;
    transition-duration: all 1s;
    transition-timing-function: ease-out;
    box-shadow: 0 5px 10px #d1d5da;
}

#table{
    height: 80vh;
    border-left: 1px solid #d1d5da;
    border-right: 1px solid #d1d5da;
    border-bottom: 1px solid #d1d5da;
    
}

#table td,th {
    text-align: left;
    padding: 10px;
}

#generatorBtn{
    height: 60px;
    font-weight: 500;
    font-size: 18px;
    margin: 20px auto;
    background: #0F172A;
    border: none;
    display: block;
    
    
}